<template>
<div>
  <div class="recommendBody">
    <v-scroll v-on:updateY="_recommendY" ref="scroll">
      <div class="recommendSlider">
        <Slider v-on:sliderNowPage="_updateNowPage" v-if="slider.length > 0">
         <div v-for="item in slider">
           <a :href="item.linkUrl">
             <img :src="item.picUrl">
           </a>
         </div>
       </Slider>
       <div class="dotsPostion">
         <v-dots :dots="realySlider" :nowPage="recommendNowPage"></v-dots>
       </div>
     </div>
     <section>
      <h1 v-if="discList.length > 0">热门推荐榜单</h1>
      <ul class="discListUl">
        <li v-for="item in discList" @click="_selectItem(item)">
          <div class="discItemLeft loading">
            <img :data-src="item.imgurl" class="lazy-img" :onerror="loading">
            <!-- <img :data-src="item.avatar" class="lazy-img" :onerror="loading"> -->
          </div>
          <div class="discItemRight">
            <p class="discCreatorName ellipsis">{{ item.creator.name }}</p>
            <p class="discDissName ellipsis">{{ item.dissname }}</p>
          </div>
        </li>
        <li id="footer" v-if="discList.length > 0">
          <span class="author">我是有底线的</span> <span class="email">email：530366891@qq.com</span>
        </li>
        <div id="footer" v-if="discList.length <= 0">正在努力获取数据</div>
      </ul>
    </section>
  </v-scroll>
</div>
<router-view></router-view>
</div>
</template>
<script>
  import Slider from 'base/slider/slider'
  import VScroll from 'base/scroll/scroll'
  import {getRecommend, getDiscList} from 'api/recommend'
  import {ERR_OK} from 'api/config'
  import VDots from 'base/dots/dots'
  import {mapMutations} from 'vuex'

  const IMAGE_HEIGHT = 100

  export default{
    data() {
      return {
        slider: [],
        discList: [],
        realySlider: 0,
        recommendNowPage: 0,
        loading: '',
        lazyLoop: 0
      }
    },
    created() {
      this._getRecommend()
      this._getDiscList()
    },
    // mounted() {
    //   requestAnimationFrame(() => {
    //     this._lazyLoadInit()
    //   })
    // },
    methods: {
      _getRecommend() {
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            this.realySlider = res.data.slider.concat([])
            this.slider = res.data.slider
            let length = this.slider.length - 1
            this.slider.unshift(this.slider[length])
            this.slider.push(this.slider[1])
          }
        })
      },
      _lazyLoadInit() {
        this.images = document.getElementsByClassName('lazy-img')
        this.lazyNumber = 0
        this.loading = 'this.src="' + require('../../assets/logo/loading.png') + '"'
        // this.domscroll = this.$refs.scroll
        this.domscrollH = this.$refs.scroll.wrapH + IMAGE_HEIGHT
        this._lazyLoad()
      },
      _lazyLoad() {
        if (!this.images[this.lazyNumber]) {
          return
        }
        if (this.images[this.lazyNumber].getBoundingClientRect().top < this.domscrollH) {
          this.images[this.lazyNumber].src = this.images[this.lazyNumber].getAttribute('data-src')
          this.images[this.lazyNumber].style.visibility = 'visible'
          this.lazyNumber++
          this._lazyLoad()
        } else {
          return
        }
      },
      _recommendY(res) {
        if (this.lazyNumber > this.images.length - 1) {
          return
        }
        if (res > 0) {
          return
        }
        window.requestAnimationFrame(() => {
          this._lazyLoad()
        })
      },
      _getDiscList() {
        getDiscList().then((res) => {
          if (res.code === ERR_OK) {
            this.discList = res.data.list
            requestAnimationFrame(() => {
              this._lazyLoadInit()
            })
          }
        })
      },
      _updateNowPage(res) {
        this.recommendNowPage = parseInt(res)
      },
      _selectItem(item) {
        this.$router.push({
          path: `/recommend/${item.dissid}`
        })
        this.setDisc(item)
      },
      ...mapMutations({
        setDisc: 'SET_DISC'
      })
    },
    components: {
      Slider,
      VScroll,
      VDots
    }
  }
</script>
<style scoped>
  .recommendSlider{
    position: relative;
  }
  .dotsPostion{
    position: absolute;
    bottom: 5%;
    left: 0;
    right: 0;
    z-index: 2;
  }
  .recommendBody{
    position: fixed;
    top: 88px;
    bottom: 0;
    width: 100%;
    min-width: 320px;
    overflow: hidden;
    background:#222;
    z-index: 1;
  }
  .recommendBody h1{
    width: 100%;
    height: 65px;
    line-height: 65px;
    text-align: center;
    font-size: 14px;
    color: #ffcd32;
  }
  .discListUl{
    width: 100%;
  }
  .discListUl li{
    width: 100%;
    height: 90px;
    overflow: hidden;
  }
  .discListUl li img{
    display: inline;
    width: 60px;
    height: 60px;
  }
  .discItemRight{
    float: right;
    height: 100%;
    width: 75%;
  }
  .discItemLeft{
    float: left;
    height: 100%;
    width: 25%;
    text-align: center;
    line-height: 90px;
  }
  .discCreatorName{
    width: 100%;
    height: 20px;
    margin: 5px 0 15px 0;
    color: #fff;
    font-size: 14px;
    line-height: 20px;
    text-align: left;
  }
  .discDissName{
   width: 100%;
   height: 20px;
   margin: 0 0 10px 0;
   color: #666;
   font-size: 13px;
   line-height: 20px;
   text-align: left;
 }
</style>


